/* -----common----- */
.App{background: #6faf5945;padding-bottom: 100px;text-align: center;width: 100%;min-height: 100vh;overflow: hidden;}
.inner{width:1200px;display: flex;justify-content: space-between;align-items: center;}
._container{display: flex;justify-content: center;}
.accordion{box-shadow: 8px 8px 25px #0000002e}
/* -----main----- */
.main{width: 100vw;height: 100vh;position: absolute;top:0;left: 0;background:#1c3841;overflow: hidden;display: flex;justify-content: space-around;align-items: center;}
.main .main_img{z-index: 9;display: flex;align-items: center;justify-content: space-around;width: 80%;}
.main .svg{position:absolute;width: 100vw;height: 100vh;}
.main .svg path{fill: #fff;position:absolute;}
.main .svg._1{top: -10vh;left: -15vw;}
.main .svg._2{bottom: 0;right: -24.5vw;}
/* -----navber----- */
.navbar{padding:15px 0 30px;z-index: 99;}
.navbar .container{width:1200px;}
.bg-light{background: transparent !important;}
.navbar-brand{margin-right: 30px;}
.nav-link{margin-right: 30px;}
.navbar-light .navbar-brand{font-weight: 400;color:#269800;font-size: 1rem;}
.navbar-light .navbar-toggler{border: none;border-radius: 0;}
.navbar-toggler:focus{box-shadow: none;background: #6faf59a1;}
.navbar-light .navbar-nav .nav-link:focus, .navbar-light .navbar-nav .nav-link:hover{color: #52c52b;}
/* -----info----- */
.accordion{width:100%;text-align: left;}
/* -----feed----- */
.feed {justify-content: center;align-items: center;flex-direction: column;}
.feed .inner{flex-wrap: wrap;}
.feed .btn{width: 18.5vw;height:45px;background:#6faf59;border:0;}
.feed .btn:hover{background: #57a43d;}
.feed .card{height: 38vh;width:30%;flex-direction: column;align-items: center;padding: 15px;justify-content: space-between;cursor: pointer;margin-bottom: 30px;}
.feed .card.green{background: #6faf59a1;}
.feed .card.red{background: #e51a498d;}
.feed .card.orange{background: #eba2349e;}
.feed .card:hover .img_wrap{background: rgba(255, 255, 255, 0.19);transition-duration: 0.3s;}
.feed .card .img_wrap{width: 80%;height:80%;border-radius: 50%;box-shadow: 11px 8px 20px 0px #00000012, -10px -10px 20px #ffffff26;overflow: hidden;display: flex;align-items: center;justify-content: center;transition-duration: 0.3s;}
.feed .card .img_wrap img{width: 70%;}
.feed .card .card-title{letter-spacing: -1.2px;font-size: 1em;font-weight: bold;color:#000000cc;margin-bottom: 0;}
/* -----species----- */
.species img{height: 380px;}
.species .accordion-body{display: flex;flex-direction: column;align-items: center;}
.species .accordion-body p{width: 100%;}
/* -----disease----- */
.disease .accordion-body{display: flex;flex-direction: column;}
.disease .img_wrap{height: 20vw;text-align: center;margin-bottom: 15px;}
.disease .img_wrap img{height: 100%;}
@media all and (max-width:1200px){
    .inner{width:90%;}
    .feed .card{width: 43vw;height:43vw;}
    .feed .card .card-title{font-size: 0.8em;}
    .feed .btn{width: 90%;}
    .species img{width:80%;height: auto;}
    .disease .img_wrap{height: 30vw;}
}
@media all and (max-width:990px){
    .navbar .container{width: 90%;max-width: 90%;padding: 0;}
    .navbar-collapse{background: #d8e9d2;}
    .main .svg{display: none;}
    .main .main_bg{width: 200%;height: 200%;background: #fff;position:absolute;top:-40%;left: -150%;transform: skewX(130deg);}
    .main_img{flex-direction: column;height: 100vh;padding-top: 100px;}
    .main_img img{width: 100%;}

}